import React, {Component} from 'react';
import './oilCoupons.css';
import util from '../../util/util';
import {getOilCoupon} from '../../services/api';
import defaultModule from '../../components/Module/defaultModule/defaultModule';
import showModule from '../../components/Module/showModule';
import OilCouponsModules from '../../components/Module/oilCoupons/oilCouponsModule'
export default class oilCoupons extends Component {
    constructor(props) {
        super(props)
    }
    toYXC = ()=>{
        if (window.location.href.indexOf('jftest') != -1) {
            window.location.href = 'http://jftest.dadi18.com/ddp-yxcat/#/yyhOrder?fromState=1';
        } else if (window.location.href.indexOf('yxcat.com.cn') != -1) {
            window.location.href = 'http://jf.dadi18.com/ddp-yxcat/#/yyhOrder?fromState=ddpactivity';
        } else {
            window.location.href = 'http://localhost:8090/ddp-yxcat/#/yyhOrder?fromState=ddpactivity';
        }
    }
    checkOilCoupon = ()=>{
        // console.log(this.refs.mobileInput.value);
        // console.log(util.checkValue('mobile',this.refs.mobileInput.value));
        if(!util.checkValue('mobile',this.refs.mobileInput.value)){
            defaultModule('提示','请输入正确手机号码',false, 1);
            return false;
        }
        getOilCoupon({mobile:  this.refs.mobileInput.value}).then(data =>{
            console.log(data);
            if(data && data.code ==='0000'){
                showModule(<OilCouponsModules/>)
            }else if(data && data.code ==='2009'){
                defaultModule('提示',data.message,false, 1,this.toYXC);
            }

        })
    }
    render() {
        return (
            <div className="oilContain">
                <img className="firstImg"
                     src="assets/img/oilCoupons/1.jpg"/>
                <div className="oilCouponsContainer">
                    <img className="oilImg"
                         src="assets/img/oilCoupons/coupon_bg.png"/>
                    <div className="btnDiv">
                        <div>
                            <img
                                src="assets/img/oilCoupons/oil_icon.png"/>
                            <div className="btn_title">
                                <div>机油优惠券</div>
                                <div>购买机油满300元立减100元</div>
                            </div>
                        </div>
                        <input
                            className="oilCouponsInput"
                            type="mobile"
                            ref="mobileInput"
                            placeholder="请输入您的手机号码"/>
                        <div className="getCouponBtn" onClick={this.checkOilCoupon}>
                            立即领取
                        </div>

                    </div>
                </div>
                <div className="oilRuleContain">
                    <img src="assets/img/oilCoupons/rule_bg.png" className="oilRule"/>
                    <br/>
                        1. 优惠券有效期1年，有效期自领券日起算。<br/>
                        2. 优惠券不可转让<br/>
                        3. 优惠券有效期内发生售后退货行为，优惠券将自动返还至您的个人账户，超出优惠券有效期发生退货行为，则优惠券视作已使用，不予返还。<br/>
                        4. 活动及优惠券最终解释权归大地电子商务公司所有。<br/>
                </div>
            </div>
        )
    }
}


