const util = {
    checkValue (type,value){
        let mobileReg=/^1[3|4|5|6|8|9][0-9]\d{4,8}$/;
        switch (type){
            case 'mobile':
                return mobileReg.test(value);
                break;
            default:
                return false;
                break;
        }
    }
};

export default util


